<script>
export default {
  name: 'MarksAmount',
  data() {
    return {
      // count: 0,
    }
  },
  props: {
    selectedObjects: {
      type: Array,
      default() {
        return []
      },
    },
  },
  methods: {
    amountObjects: function () {
      const objectsLength = this.selectedObjects.length
      if (objectsLength > 9) {
        return '9+'
      } else {
        return objectsLength
      }
    },
  },
  computed: {
    // amountObjects: function () {
