  },
}
</script>

<style lang="scss">
.selected-marks {
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
  width: 52px;
  height: 52px;
  border-radius: 50%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: 575.98px) {
    width: 40px;
    height: 40px;
  }
  &__counter {
    position: absolute;
    top: -6px;
    right: -6px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background: #00404e;
    border: 1px solid #ffffff;
    color: #ffffff;
    text-align: center;
    font-size: 14px;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 575.98px) {
      top: -11px;
      right: -9px;
    }
    &.active {
      background: #db3831;
    }
  }

  &__icon {
    display: flex;
    flex: none;
    @media (max-width: 575.98px) {
      svg {
        width: 24px;
        height: 24px;
      }
    }
  }
}
